$background-color-light: #fff !default;
$background-color-base: #fafafa !default;
$background-color-dark: #eaeaea !default;

$body-background-color: $background-color-base !default;

$sidebar-width: 220px;
$sidebar-background-color: $background-color-base !default;

$border-color: #d6d6d6 !default;

$text-color-light: #555 !default;
$text-color-base: #333 !default;
$text-color-dark: #111 !default;

$link-color: #7baaf7 !default;

$icon-color: #919191 !default;
$shadow-color: #fafbfc !default;

$primary: $text-color-dark !default;

$font-size-base: 1rem !default;
$font-size-lg: $font-size-base * 1.25 !default;
$font-size-sm: $font-size-base * 0.875 !default;

@import "../../styles/animations/rotate";

@import "../../styles/mixins/geometry";
@import "../../styles/mixins/scrollbar";
@import "../../styles/mixins/text";

$green: #1ff042;

@keyframes cursor-blink {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

.terminal{
	position: relative;
	min-height: 100vh;
	padding: 4rem;
	background-color: #001529;

	.prompt{
		color: $green;
		display: block;
		font-family: 'AndaleMono', monospace;
		font-weight: bold;
		text-transform: uppercase;
		font-size: 0.9em;
		letter-spacing: 0.15em;
		white-space: pre-wrap;
		text-shadow: 0 0 2px rgba($green,0.75);
		line-height: 1;
		margin-bottom: 0.75em;

		&:before {
			content: '> ';
			display: inline-block;
		}
	}

	.new-output{
		display: inline-block;
		&:after {
			display: inline-block;
			vertical-align: -0.15em;
			width: 0.75em;
			height: 1em;
			margin-left: 5px;
			background: $green;
			box-shadow: 1px 1px 1px rgba($green,0.65), -1px -1px 1px rgba($green,0.65), 1px -1px 1px rgba($green,0.65), -1px 1px 1px rgba($green,0.65);
            animation: cursor-blink 1.25s steps(1) infinite;
			content: '';
		}
	}
}